@import '@/assets/sass/base/_reset.scss';
@import '@/assets/sass/mixins/_triangle.scss';



.gt-tooltip {
  position: relative;
  display: inline-block;
  
  .gt-arrow {
    position: absolute;
  }

  .gt-tooltip-content, .gt-tt-content {
    //display: none;
    position: absolute;
    box-sizing: border-box;
    padding: 0px 15px; 
    min-width: 100px;
    background: $dark-primary-color;    
    text-align: center;
    user-select: none;
    cursor: default;
    z-index: 1000;
  }

  &[data-placement="right"] {
    .gt-triangle {
      position: absolute;
      @include triangle(left, 6px, 6px, $dark-primary-color);
    }
  }

  &[data-placement="left"] {
    .gt-triangle {
      position: absolute;
      @include triangle(right, 6px, 6px, $dark-primary-color);
    }
  }

  &[data-placement="bottom"] {
    .gt-triangle {
      position: absolute;
      @include triangle(top, 6px, 6px, $dark-primary-color);
    }
  }

  &[data-placement="top"] {
    .gt-triangle {
      position: absolute;
      @include triangle(bottom, 6px, 6px, $dark-primary-color);
    }
  }

  .gt-triangle {
    position: absolute;
    @include triangle(bottom, 6px, 6px, $dark-primary-color);
  }

  // &.gt-tooltip-primary {
  //   .gt-tt-content {
  //     background: $primary-color;
  //   }
  //   &[data-placement="right"] {
  //     .gt-triangle {
  //       @include triangle(left, 6px, 6px, $primary-color);
  //     }
  //   }
  //   &[data-placement="left"] {
  //     .gt-triangle {
  //       @include triangle(right, 6px, 6px, $primary-color);
  //     }
  //   }
  //   &[data-placement="bottom"] {
  //     .gt-triangle {
  //       @include triangle(top, 6px, 6px, $primary-color);
  //     }
  //   }
  //   &[data-placement="top"] {
  //     .gt-triangle {
  //       @include triangle(bottom, 6px, 6px, $primary-color);
  //     }
  //   }
  // }



  $types: (
    'primary' : $primary-color,
    'warning' : $warning-color,
    'danger' : $danger-color,
    'info' : $info-color
  );


  @each $key, $colorType in $types {

    &.gt-tooltip-#{$key} {
      .gt-tt-content {
        background: #{$colorType};
      }
      &[data-placement="right"] {
        .gt-triangle {
          @include triangle(left, 6px, 6px, #{$colorType});
        }
      }
      &[data-placement="left"] {
        .gt-triangle {
          @include triangle(right, 6px, 6px, #{$colorType});
        }
      }
      &[data-placement="bottom"] {
        .gt-triangle {
          @include triangle(top, 6px, 6px, #{$colorType});
        }
      }
      &[data-placement="top"] {
        .gt-triangle {
          @include triangle(bottom, 6px, 6px, #{$colorType});
        }
      }
    }
  }

}
