
          @import "@/assets/sass/base/_variables.scss";
          @import "@/assets/sass/mixins/_clearfix.scss";
          @import "@/assets/sass/mixins/_truncate.scss";
          @import url(https://static.gabia.com/fonts/icons/v1.0.1/style.css);
          





















































.gt-btn {
  display: inline-flex;
  flex-shrink: 0;
  align-items: center;
  position: relative;
  color: #999;
  background: #fff;
  height: 40px;
  min-width: 50px;
  padding-left: 16px;
  padding-right: 16px;
  font-size: 16px;
  border-radius: 0;
  font-weight: 400;
  border: 1px solid transparent;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  box-sizing: border-box;
  justify-content: center;
  vertical-align: middle;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
  user-select: none;
  outline: none;

  /* hover */
  &:hover,
  &:focus {
    color: #666;
    background: #f8f8f8;
  }

  /* disabled */
  &:disabled {
    cursor: default;
    opacity: .3;
    background: none;
  }
  &:hover[disabled] {
    color: #999;
  }

  /* outline button */
  &.gt-btn-outline {
    border-color: #ddd;

    &:hover, &:focus {
      border-color: #aaa;
      background-color: #fff;
    }

    &:hover[disabled], &:focus[disabled] {
      border-color: #ddd;
    }
  }

  /* primary */
  &.gt-btn-primary {
    background-color: #2985db;
    border-color: #2985db;
    color: #fff;

    &:hover, &:focus {
      background-color: #056ac9;
      border-color: #056ac9;
      color: #fff;
    }

    &:hover[disabled], &:focus[disabled] {
      background-color: #2985db;
      border-color: #2985db;
      color: #fff;
    }
  }

  /* secondary */
  &.gt-btn-secondary {
    background-color: #6c757d;
    border-color: #6c757d;
    color: #fff;

    &:hover, &:focus {
      background-color: #5a6268;
      border-color: #5a6268;
      color: #fff;
    }

    &:hover[disabled], &:focus[disabled] {
      background-color: #5a6268;
      border-color: #5a6268;
      color: #fff;
    }
  }

  /* danger */
  &.gt-btn-danger {
    background-color: #dc3545;
    border-color: #dc3545;
    color: #fff;

    &:hover, &:focus {
      background-color: #c82333;
      border-color: #c82333;
      color: #fff;
    }

    &:hover[disabled], &:focus[disabled] {
      background-color: #dc3545;
      border-color: #dc3545;
      color: #fff;
    }
  }

  /* icon button */
  &.gt-btn-icon {
    width: 40px;
    min-width: 40px;
    height: 40px;
    border-radius: 50%;
  }
}


